export default {

    name      : 'HelloWorld',
    mixins    : [],
    components: {},

    props: {
        name: {
            type   : String,
            default: 'World'
        }
    },

    data () {
        return {
            reversed: false
        };
    },

    computed: {
        display () {
            const str = `Hello ${ this.name }`;
            return this.reversed
                ? str
                    .split ( '' )
                    .reverse ()
                    .join ( '' )
                : str;
        }
    },

    methods: {

        reverse () {
            this.reversed = !this.reversed;
        }

    }

};
